<template>
    <div>
        <div class="kd-title-content-wrapper kd-page-padding wrap-info">
            <text-table :data="data"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="data.length === 0"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import textTable from '@/components/text-table';
import { confirmReplacement, getCertificates } from '@/service/traffic/driver/replacement';
import audioCar from '@/assets/media/trafic/400.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'DriverExchangeEvidenceConfirmInfo',
    components: { textTable },
    computed: {
        ...mapState('replacement', {
            data: 'certificatesInfo'
        })
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.initData();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('replacement', ['setCertificatesInfo']),
        async nextStep () { // 下一步
            const res = await confirmReplacement({});
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        preStep () { // 上一步
            this.changeStep('up');
        },
        async initData () {
            const res = await getCertificates({});
            if (res && res.code === '0') {
                this.setCertificatesInfo(res.result);
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }

    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: flex-start;
    }

</style>
