<template>
    <div>
        <div class="kd-page-title">
            邮寄信息
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <el-form
                class="mailWrap"
                label-width="15vw"
            >
                <el-form-item label="收件人：">
                    <span>{{ identifyCard.partyName }}</span>
                </el-form-item>
            </el-form>
            <el-form
                class="mailWrap"
                label-width="15vw"
            >
                <el-form-item label="联系电话：">
                    <kd-full-input
                        :ime-type="3"
                        class="recipientsPhone"
                        :watch-data="getRecipientsPhone"
                        placeholder="请输入电话"
                        :default-content="recipientsPhone"
                        :limit-number="11"
                    />
                </el-form-item>
            </el-form>
            <el-form
                class="mailWrap"
                label-width="15vw"
            >
                <el-form-item label="收件地址：">
                    <kd-full-input
                        class="mailingAddress"
                        :watch-data="getMailingAddress"
                        placeholder="请输入地址"
                        :default-content="mailingAddress"
                        :rows="5"
                        :is-texture="true"
                    />
                </el-form-item>
            </el-form>
            <div class="red-tip">
                {{ errTip }}
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="提交"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { maillInfo } from '@/service/traffic/driver/replacement';
import { keyboardHide } from '@/service/common';
import { checkPhone } from '@/utils/tools';
import audioCar from '@/assets/media/trafic/401.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'DriverExchangeEvidenceInputAddress',
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'

        })
    },
    data () {
        return {
            editState: false,
            mailingAddress: '',
            recipientsPhone: '',
            errTip: ''
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        getRecipientsPhone (data) {
            this.recipientsPhone = data;
        },
        getMailingAddress (data) {
            this.mailingAddress = data;
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (!this.recipientsPhone || !this.mailingAddress) {
                this.errTip = '请将信息填写完整';
                return false;
            }
            if (checkPhone(this.recipientsPhone)) {
                this.errTip = '';
            } else {
                this.errTip = '请检查手机号是否正确';
                return false;
            }
            const res = await maillInfo({
                mobile: this.recipientsPhone,
                address: this.mailingAddress
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else {
                this.changeTipPage({
                    message: res.message,
                    enableBack: true
                });
            }
        }
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
    },
    beforeDestroy () {
        this.audioCar.destroy();
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 4vw;
    }

    .editBut {
        width: 15vw;
        text-align: right;
    }

    .recipientsPhone {
        color: #606266;
        width: 100%;
        font-size: 2.4vh;
    }

    .mailingAddress {
        display: block;
        width: 100%;
        font-size: 2.4vh;
        color: #606266;
    }

    ::v-deep .mailWrap .el-form-item__label {
        font-size: 2.4vh;
        color: #fff;
        height: 60px;
        line-height: 60px;
    }

    ::v-deep .mailWrap .el-input__inner {
        height: 4vh;
        margin: 1vh 0;
    }

    ::v-deep .mailWrap .el-form-item__content span {
        font-size: 2.4vh;
        color: #fff;
        height: 6vh;
        line-height: 6vh;
    }

    .red-tip {
        margin-left: 15vw;
        height: 15px;
        line-height: 15px;
        font-size: 20px;
        color: #ff2306;
    }
</style>
