import request from '@/utils/request';

// 驾驶证换证业务 验证接口(是否满足换证条件)
export const checkIsSatisfy = (params) => {
    return request.ajax('/ssm/jg/drivingLicenceChangeRecord/verify', 'post', params);
};

// 驾驶证换证业务 获取证件信息（字段同延期换证）
export const getCertificates = (params) => {
    return request.ajax('/ssm/jg/drivingLicenceChangeRecord/information', 'get', params);
};

// 驾驶证换证业务 信息确认
export const confirmReplacement = (params) => {
    return request.ajax('/ssm/jg/drivingLicenceChangeRecord/information/confirm', 'get', params);
};

// 驾驶证换证业务 邮寄信息
export const maillInfo = (params) => {
    return request.ajax('/ssm/jg/drivingLicenceChangeRecord/mailFillIn', 'post', params);
};

// 驾驶证换证业务 提交数据
export const completeUpload = (params) => {
    return request.ajax('/ssm/jg/drivingLicenceChangeRecord/finish', 'post', params);
};

// 驾驶证换证业务 保存现场人照片
