<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <confirm-info v-if="currentStep === steps.findIndex(step=>step.key === 'confirmInfo')"/>
        <input-address v-if="currentStep === steps.findIndex(step=>step.key === 'inputAddress')"/>
        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'processComplete')"
            :handle-business="completeBusiness"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="data"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import confirmInfo from './components/confirm-info';
import inputAddress from './components/input-address';
import complete from '@/components/complete';
import process from '@/components/process';
import { checkIsSatisfy, completeUpload } from '@/service/traffic/driver/replacement';
import { REPLACEMENT_TYPES } from '@/constant/constant';

export default {
    name: 'DriverExchangeEvidenceIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        confirmInfo,
        inputAddress,
        complete,
        process
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('replacement', {
            data: 'completeInfo'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('replacement', ['setBusinessInfo', 'setCompleteInfo']),
        async handleBusiness () {
            const res = await checkIsSatisfy({
                identifiedPhoto: this.binocularCamera,
                type: REPLACEMENT_TYPES[this.businessTypeCode]
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async completeBusiness () {
            const res = await completeUpload({});
            if (res && res.code === '0') {
                this.setCompleteInfo(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        this.setSteps([
            {
                name: '业务须知',
                isActive: true,
                visible: true,
                key: 'serverInfo'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'process'
            },
            {
                name: '信息确认',
                isActive: false,
                visible: true,
                key: 'confirmInfo'
            },
            {
                name: '邮寄信息',
                isActive: false,
                visible: true,
                key: 'inputAddress'
            },
            {
                name: '邮寄信息',
                isActive: false,
                visible: false,
                key: 'processComplete'
            },
            {
                name: '完成提交',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};
</script>
